import Swal from 'sweetalert2';
import storage from '../utils/storage';

const welcome = () => {
    Swal.fire({
        title: 'Cho mng bn n vi zingmp3 FAKE ^^;',
        input: 'text',
        inputPlaceholder: 'Vui lng nhp nickname ...',
        confirmButtonText: 'Tip tc',
        allowOutsideClick: false,
        allowEscapeKey: false,
        confirmButtonColor: 'var(--purple-primary)',
        inputValidator: value => {
            if (!value) {
                return 'Nickname khng c  trng !'
            }
        }
    })
    .then((result) => {
        storage.setUsername(result.value);
        return Swal.fire({
            html: 'Trang web ny c xy dng bng <span class="font-bold text-blue-500">React</span> v ly nguyn mu t <span class="font-bold text-red-500">zingmp3.vn</span> , d liu c update ngy 20/12/2021',
            confirmButtonText: 'Tip tc',
        })
    })
    .then(() => {
        return Swal.fire({
            html: ' c tri nghim tt nht vui lng truy cp bng<span class="font-bold text-red-500"> Window - Chrome</span>, cc trnh duyt khc c th xy ra cc li khng mong mun',
            confirmButtonText: 'Tip tc',
        })
    })
    .then(() => {
        return Swal.fire({
            html: 'C 2 phn nhc chnh cho bn la chn l <span class="font-bold text-red-500">#Zingchart</span>, <span class="font-bold text-red-500">Nhc mi</span>, v <span class="font-bold text-red-500">C nhn</span>  qun l cc bi ht ca bn, cn cc UI cn li ch  trng cho p ^^',
            confirmButtonText: 'Tip tc',
        })
    })
    .then(() => {
        return Swal.fire({
            html: ` s dng chc nng <span class="font-bold text-red-500">Tm kim bi ht</span> v <span class="font-bold text-red-500">xem MV</span> vui lng c k hng dn`,
            confirmButtonText: 'Tip tc',
        })
    })
    .then(() => {
        Swal.fire({
            text: 'Chc bn nghe nhc vui v <3',
            confirmButtonText: 'Tip tc',
        })
    })
}
export default welcome;